/* App.css */

/* Define global styles here */
/* body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  } */
  body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 1em;
    line-height: 1.5;
    padding: 0;
    margin: 0;
    color: #d1d0cf;
    background: #141414;
}
  
  /* Style the main container for your app */
  .App {
    text-align: center;
  }
  
  .rowTest {
    display: flex;
    flex-wrap: wrap;
    margin-right: -1em;
    margin-left: -1em;
}

.alignEach {
    padding-right: 1em;
    padding-left: 1em;
    margin-bottom: 30px;
}

/* .img-box {
    height: 205px!important;
